.submit{
	width: auto;
	height: 45px;
	border: 3px solid #313131;
	font-weight: bold;
	-webkit-transition: 0.2s all;
	-o-transition: 0.2s all;
	transition: 0.2s all;
	cursor: pointer;
}

.submit:hover{
	border: 3px solid #494949;
}


